import { BaseTypes, DynamicObject } from 'lance-gg';

export default class Explosion extends  DynamicObject {

    static get netScheme() {
        return Object.assign({
            radius: { type: BaseTypes.TYPES.FLOAT32 },
        }, super.netScheme);
    }

    constructor(gameEngine, options, props) {
        super(gameEngine, options, props);
        this.class = Explosion;
        this.type = "Explosion";
        this.isStatic = true;
        // this.splatter = [
        //     [Math.random()*5, Math.random()*5, Math.random()*5],
        //     [Math.random()*5, -Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, -Math.random()*5, Math.random()*5],
        //     [Math.random()*5, Math.random()*5, Math.random()*5],
        //     [Math.random()*5, -Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, -Math.random()*5, Math.random()*5],
        //     [Math.random()*5, Math.random()*5, Math.random()*5],
        //     [Math.random()*5, -Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, Math.random()*5, Math.random()*5],
        //     [-Math.random()*5, -Math.random()*5, Math.random()*5],
        // ]
    };

    syncTo(other) {
        super.syncTo(other);
    }

    collidesWith(other){
        return false;
    }

}